import { PatternSchema, PatternTextOverview } from "@tanyoknits/shared";
import { VestMeasurement } from "../../utils/sizes";
import { VestPattern } from "../../utils/vestPatternMaker";
import { overview } from "./pattern";
import PatternOverview from "../../components/PatternOverview";

export interface InstructionProps {
  schema: VestPattern;
  measurement: VestMeasurement;
}

interface Props {
  menu: number;
  size: number;
}

export default function Instruction({ menu, size }: Props) {
  if (menu === 0) {
    return <PatternOverview overview={overview} />;
  } else if (menu === 1) {
    return <div>back</div>;
  } else if (menu === 2) {
    return <div>front</div>;
  } else if (menu === 3) {
    return <div>body</div>;
  } else {
    return <div>bands</div>;
  }
}
